require('./styles/index.scss');

document.addEventListener('DOMContentLoaded', function() {
  preloadImages();
  $('.btn-open-nav-menu').click(() => {
    if ($('.mobile-nav-list').hasClass('open')) {
      $('.mobile-nav-list').removeClass('open');
    } else {
      $('.mobile-nav-list').addClass('open');
    }
  });
});

function preloadImages() {
  const arr = [
    './images/Icon_logos.png',
    './images/Image_homepage.png',
    './images/Image_homepage_side.png',
    './images/Icon_headline.png',
    './images/Icon_home_calendar1.png',
    './images/Icon_home_calendar2.png',
    './images/Icon_home_calendar3.png',
    './images/Icon_home_calendar4.png',
  ];
  const result = {};
  arr.forEach(url => {
    const image = new Image();
    image.onload = () => {
      result[url] = true;
      if (Object.keys(result).length === arr.length) finishMainLoading();
    };
    image.onerror = () => {
      result[url] = false;
      if (Object.keys(result).length === arr.length) finishMainLoading();
    };
    image.src = url;
  });
}

function finishMainLoading() {
  $('#main-loading').addClass('hide');
  $('.waiting-loading').removeClass('waiting-loading');
}
